import { Component, OnInit } from '@angular/core';
import { MessagesService } from '../message-service/messages.service';
import { HeroService } from '../hero-service/hero.service';
import { Hero } from '../hero';
import {FormControl, FormGroup} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';

@Component({
  selector: 'app-hero-home',
  templateUrl: './hero-home.component.html',
  styleUrls: ['./hero-home.component.css']
})
export class HeroHomeComponent implements OnInit {

  public heros: Hero[];

  //
  myControl: FormControl = new FormControl();
  filteredOptions: Observable<Hero[]>;

  constructor(private heroService: HeroService
    ,private messageService: MessagesService
     ) { 
       
     }

    

  ngOnInit() {
    this.getHeros();
    console.log("H 2");
    // if(this.heros) {
    //   console.log("H 3")
    //   this.filteredOptions = this.myControl.valueChanges.pipe(
    //     startWith(''),
    //     map(val => this.filter(val))
    //   );
    // }
    
  }

  public filter(val: string) {
    return this.heros.filter(option => option.name.toLowerCase().indexOf(val.toLowerCase()) === 0);
  }

  public getHeros(): void {
    this.messageService.add("Top hero fetch start");
    this.heroService.getHeros().subscribe(heros => {
      console.log("heros api", heros);
      this.heros = heros;

      //
      this.filteredOptions = this.myControl.valueChanges.pipe(
        startWith(''),
        map(val => this.filter(val))
      );
      //
    });

  }
  

}
