import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { RouterModule } from '@angular/router';
import  { HttpModule } from '@angular/http'

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { HomeComponent } from './home/home.component';
import { FeaturesComponent } from './features/features.component';
import { StarMarkComponent } from './star-mark/star-mark.component';
import { PostComponent } from './post/post.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms'

import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { MatInputModule, 
         MatButtonModule,
         MatDividerModule,
         MatExpansionModule,
         MatGridListModule,
         MatIconModule,
         MatListModule,
        } from '@angular/material';
import {MatFormFieldModule} from '@angular/material/form-field';
import {MatCardModule} from '@angular/material/card';
import { HerosComponent } from './heros/heros.component';
import { HeroDetailComponent } from './hero-detail/hero-detail.component';
import { HeroService } from './hero-service/hero.service';
import { MessagesComponent } from './messages/messages.component';
import { MessagesService } from './message-service/messages.service';
import { DashboardComponent } from './dashboard/dashboard.component';
import { HeroHomeComponent } from './hero-home/hero-home.component';
import { HttpClientInMemoryWebApiModule } from 'angular-in-memory-web-api';
import { HttpClientModule } from '@angular/common/http';
import { InMemoryWebApiModule } from 'angular-in-memory-web-api';
import { InMemoryDataService }  from './in-memory-data.service';
import {MatAutocompleteModule} from '@angular/material/autocomplete';

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HomeComponent,
    FeaturesComponent,
    StarMarkComponent,
    PostComponent,
    HerosComponent,
    HeroDetailComponent,
    MessagesComponent,
    DashboardComponent,
    HeroHomeComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    MatInputModule,
    MatButtonModule,
    MatFormFieldModule,
    MatCardModule,
    MatDividerModule,
    MatExpansionModule,
    MatGridListModule,
    MatIconModule,
    MatListModule,
    MatAutocompleteModule,
    RouterModule.forRoot([
      {
        path: 'home',
        component: HomeComponent
      },
      {
        path: 'features',
        component: FeaturesComponent
      },
      {
        path: 'starmark',
        component: StarMarkComponent
      },
      {
        path: 'hero-dashboard',
        component: DashboardComponent,
        children: [
          { path: '', component: HeroHomeComponent },
          { path: 'dashboard', component: HeroHomeComponent },
          { path: 'detail/:id', component: HeroDetailComponent },
          { path: 'heroes', component: HerosComponent }
        ]
      },
      {
        path: '**',
        component: PostComponent
      }
    ]),
    HttpClientModule,
    HttpClientInMemoryWebApiModule.forRoot(InMemoryDataService)
  ],
  providers: [
    HeroService,
    MessagesService,
    InMemoryDataService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
