import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { post } from 'selenium-webdriver/http';

@Component({
  selector: 'app-star-mark',
  templateUrl: './star-mark.component.html',
  styleUrls: ['./star-mark.component.css']
})
export class StarMarkComponent {

  starBool: boolean = false;
  emptyStar: string = 'glyphicon glyphicon-star-empty';
  filledStar: string = 'glyphicon glyphicon-star';
  counter: number = 10;
  starName: string = 'mark 1';
  //@Input() obj: object;
  //@Output() change =  new EventEmitter();
  
  onStarClick() {
    this.starBool = !this.starBool;

    if (this.starBool) {
      this.counter += 1
    } else {
      this.counter -= 1
    }

    //this.change.emit(this.starBool);
    
  }

  post = {
    name: "yash",
    isFav: false,
    starMark: "Mark - 1"
  }

}
